<template>
    <v-card
        outlined
    >
        <v-card-title>API Requests</v-card-title>
        <v-card-subtitle>Your remaining API requests.</v-card-subtitle>
        <v-card-text>
            <div
                class="black--text text-h3 mb-5"
            >
                {{ remainingApiRequests }} / {{ totalApiRequests }}
            </div>
            <v-progress-linear :value="(remainingApiRequests / totalApiRequests) * 100"></v-progress-linear>
        </v-card-text>
    </v-card>
</template>

<script>
export default {
    name: 'ApiCard',

    props: {
        totalApiRequests: Number,
        remainingApiRequests: Number,
    }
}
</script>