<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
    >
    <v-btn
              dark
              icon
              v-on:click="drawer = !drawer"
            >
              <v-icon>mdi-menu</v-icon>
    </v-btn>

    <div>
      BAR Web Client

    </div>
    </v-app-bar>
    
     <v-navigation-drawer
        temporary
        v-model="drawer"
        fixed
      >
        <v-list
          dense
          nav
          class="py-0"
        >
          <v-list-item two-line>
            <v-list-item-content>
              <v-list-item-title>BAR 2.0 Web Client</v-list-item-title>
              <v-list-item-subtitle>Bann's Automatic Recruitment</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>

          <v-divider></v-divider>

          <v-list-item
            v-for="(item, idx) in navItems"
            :key="idx"
            @click="navLocation = item.location; drawer = false;"
            link
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>

    <v-main 
      class="d-flex pr-10 pl-10 main-width"
    >
      <v-scroll-y-transition group>
        <template v-if="navLocation == 0">
          <div :key="0">
            <v-row class='mt-5'>
              <ApiCard 
                :totalApiRequests="this.api.totalRequests"
                :remainingApiRequests="this.api.remainingRequests"
                class="max-width"
              ></ApiCard>
            </v-row>
            <v-row class="mt-5">
              <SentMessages class="max-width" :items="sentMessages"></SentMessages>
            </v-row>
             <v-btn
              bottom
              right
              fab
              class="primary white--text"
              fixed
              @click="loadData()"
            >
              <v-icon>
                  mdi-refresh
              </v-icon>
            </v-btn>
          </div>
        </template>
        
        <template v-if="navLocation == 1">
          <Configuration  :key="1"></Configuration>
        </template>

        <template v-if="navLocation == 2">
          <About :version="version" :key="2"></About>
        </template>
      </v-scroll-y-transition>
    </v-main>
  </v-app>
</template>

<script>

import ApiCard from './components/ApiCard';
import SentMessages from './components/SentMessages';
import Configuration from './components/Config';
import About from "./components/About";

export default {
  name: 'App',

  components: {
    ApiCard,
    SentMessages,
    Configuration,
    About
  },

  data: () => ({
      version: "Alpha 0.9 Release",

      drawer: false,
      navItems: [
        { title: 'Dashboard', icon: 'mdi-view-dashboard', location: 0 },
        { title: 'Configuration', icon: 'mdi-cog', location: 1 },
        { title: 'About', icon: 'mdi-help-box', location: 2 },
      ],

      navLocation: 0,

      recievedMessages: [],

      api: {
        totalRequests: 0,
        remainingRequests: 0,
      },

      config: {}
  }),

  computed: {
    sentMessages: function ()
    {
      return this.convertToItems(this.recievedMessages);
    }
  },

  methods:
  {
    convertToItems(messages)
    {
      var messageItems = [
         { header: 'Sent Messages' }
      ];

      messages.forEach(message =>
      {
        var messageItem = 
        {
          avatar: message.successful ? 'imgs/success.jpg' : 'imgs/failed.jpg',
          title: message.nation.leader,
          subtitle: `They created ${message.nation.name} on ${new Date(message.nation.founded).toLocaleString()}`,
          link: `https://politicsandwar.com/nation/id=${message.nation.id}`
        }

        messageItems.push(messageItem);
      });

      return messageItems;
    },

    async get(url)
    {
          return new Promise((resolve, reject) => 
          {
              var request = new XMLHttpRequest();

              request.onreadystatechange = () => 
              {
                  if (request.readyState == 4)
                  {
                      if (request.status == 200)
                      {
                          var response = JSON.parse(request.responseText);

                          resolve(response);
                      }
                      else
                      {
                          reject(request);
                      }                            
                  }
              }

              request.open("GET", url, true);
              request.send({});

          });
    },

    async loadData()
    {
      var data = await this.get('/api/web-db').catch();

      this.recievedMessages = data.messages;

      this.api.totalRequests = data.apiKeyDetails.totalRequests;
      this.api.remainingRequests = data.apiKeyDetails.remainingRequests;
    }

  },

  mounted: function()
  {
    this.loadData();
  }
};
</script>

<style>
  .max-width
  {
    width: 100%;
  }

  .main-width
  {
    width: 1200px;
    margin: auto;
  }
</style>
