<template>
    <div>
        <v-slide-y-transition>
            <v-list 
                three-line 
                outlined 
                rounded
            >
                <template v-for="(item, index) in itemsSliced">
                    <v-subheader
                    v-if="item.header"
                    :key="index"
                    v-text="item.header"
                    ></v-subheader>

                    <v-list-item
                    v-else
                    :key="index"
                    :href="item.link"
                    target="_blank"
                    >
                        <v-list-item-avatar>
                            <v-img :src="item.avatar"></v-img>
                        </v-list-item-avatar>

                        <v-list-item-content>
                            <v-list-item-title v-html="item.title"></v-list-item-title>
                            <v-list-item-subtitle v-html="item.subtitle"></v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>

                    <v-divider
                    v-if="index != itemsSliced.length - 1 && !item.header"
                    :key="index"
                    :inset="item.inset"
                    ></v-divider>
                </template>

                <div v-if="itemsSliced.length == 0 || (itemsSliced[0].header && itemsSliced.length == 1)"  class="text-center">
                     No sent messages yet.
                </div>
            </v-list>
        </v-slide-y-transition>

        <div class="text-center mt-12 mb-12">
            <v-pagination
            v-model="page"
            :length="Math.ceil(items.length / 10)"
            ></v-pagination>
        </div>
    </div>
</template>

<script>
export default {
    name: 'SentMessages',

    props: {
        items: Array
    },

    data()
    {
        return {
            page: 1,
            itemsPerPage: 10
        }
    },

    computed:
    {
        itemsSliced: function()
        {
            var page = this.page - 1;

            var newItems = this.items.slice(page * 10, (page + 1) * 10);

            return newItems;
        }
    },

    watch:
    {
        items:
        {
            deep: true,

            handler(val)
            {
                this.items = val; 
            }
        }
    }
}
</script>